.carousel {
  height: 74vh;
  width: 90%;
  max-width: 1140px;
  margin: 0 auto;
  max-height: 850px;

  @include respond(tablet) {
    height: 750px;
  }
  @include respond(tablet) {
    height: 700px;
  }
}
.carousel__track-wrapper {
  height: 100%;
  width: 100%;
  overflow: hidden;

  .carousel__track {
    position: relative;
    height: 100%;
    width: 100%;
    transition: all 0.3s ease;
  }
}

.carousel__slide {
  position: absolute;
  height: 100%;
  width: 100%;
  // background-color: tomato;
  // border: 1px solid #000;

  img {
    max-width: 600px;

    @include respond(big-desktop) {
      width: 600px;
    }
    @include respond(laptop) {
      width: 450px;
    }
    @include respond(tablet) {
      width: 400px;
      float: none;
    }
    @include respond(phone) {
      width: 70%;
    }
    @include respond(phone-sm) {
      width: 90%;
    }
  }
  .content {
    width: 38%;

    @include respond(laptop) {
      width: 41%;
    }
    @include respond(tablet) {
      width: 100%;
    }
    h2 {
      @include respond(tablet) {
        margin-top: $margin-md;
      }
    }
    h3,
    p {
      margin-top: $margin-lg + 16px;
      @include respond(laptop) {
        margin-top: $margin-md + 16px;
      }
      @include respond(tablet) {
        margin-top: $margin-md;
      }
    }
  }
}
