.video-popup {
  cursor: pointer;
  .play-circle {
    display: inline-block;
    vertical-align: middle;
    img {
      @include respond(phone) {
        width: 28px;
        height: 28px;
      }
    }
  }
  h2 {
    font-size: 22px;
    font-weight: 700;
    text-transform: uppercase;
    margin-left: $margin-sm;
    vertical-align: middle;

    @include respond(tablet) {
      font-size: 18px;
    }
    @include respond(tablet) {
      font-size: 14px;
    }
  }
}

.video-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 25;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in;
  transform: scale(0);

  .close-btn {
    position: absolute;
    top: 10%;
    right: 25px;
    color: #fff;
    cursor: pointer;
    @include respond(big-desktop) {
      font-size: 40px;
    }
    @include respond(laptop) {
      font-size: 30px;
    }
    @include respond(tablet) {
      font-size: 20px;
    }
    @include respond(phone) {
      font-size: 16px;
    }
  }
  .video {
    width: 90%;
    max-width: 800px;
    height: 70%;
    max-height: 540px;
    border-radius: 5px;
    outline: none;
    transition: all 0.3s ease-in;
  }
  &.show {
    transform: scale(1);
  }
}
