.menu-btn {
  display: none;
  position: relative;
  z-index: 30;
  @include respond(tablet) {
    display: inline-block;
  }
}

.nav {
  @include respond(tablet) {
    display: none;
  }
  &__list {
    li {
      display: inline-block;
      line-height: 35px;
      &:hover .nav__link::after {
        width: 100%;
      }

      .nav__link {
        position: relative;

        &::after {
          content: "";
          width: 0;
          position: absolute;
          height: 2px;
          background: #fff;
          left: 0;
          bottom: 3px;
          transition: all 0.3s ease-in;
        }
      }

      &:not(:last-child) {
        margin-right: $margin-lg;

        @media screen and (max-width: 1024px) {
          margin-right: $margin-md;
        }
      }
    }
  }
}

// mobile navigation

.mobile-nav {
  transform: translateX(-110%);
  position: fixed;
  width: 100%;
  height: 100%;
  background: $color-primary;
  top: 0;
  bottom: 0;
  left: 0;
  transition: all 0.3s ease-in;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .close {
    position: absolute;
    left: 30px;
    top: 30px;
    cursor: pointer;
  }
  &__list {
    // padding-top: calc(2 * $padding-lg);
    li {
      &:not(:last-child) {
        margin-bottom: $margin-lg;
        @include respond(phone) {
          margin-bottom: $margin-md;
        }
      }
    }
  }
}

.active {
  transform: translateX(0);
}
